<template>

 <div className="cartcontainer"> 

  <div v-if="!Object.keys(mycart).length">
   <h3> There are no items in your cart </h3>
  </div> 
   
    <br className="secondspace"/> 
   <hr/>
    <br/> 
    
   <div v-if="Object.keys(mycart).length"> 
   
    <div className="cflex"> 
    <div className="pimgc"> <h4> Product img </h4>  </div> 
    <div className="dc"> <h4> Description </h4>  </div> 
    <div className="pc"> <h4 className="pcc"> Price </h4>  </div> 
    <div className="dec"> <h4> Delete </h4>  </div> 
    </div> 
   <div v-for="(onecart, key, i) in mycart" :key="i" className="cflex">

   <div className="pimgc"> <img :src="getimage(key)"/> </div>
   <div className="dc">  
      <h4> {{key}} </h4>
      <div className="dcdescription"> 
      <h3>{{getdesc(key)}}</h3>
      <h3> Quantity:   {{onecart}} </h3>
      <h4> Price: N{{ getprice(key) }} </h4> 
       </div> 
      </div>
    <div className="pc"> <h4> N{{ onecart * getprice(key) }} </h4> </div> 
    <div className="dec mybtn"> <button @click="Delete(onecart)"> X </button> </div> 
   
   </div>

    <hr/>
    <br/> 
    <br/> 

     <div className="pcdiv"> 
  
   <div className="productcategoriess">
   <button> Clear Cart </button>
   </div>

    <div className="productcategoriesss">
   <button> Update Cart </button>
   </div>

   </div> 

   <hr> 

  <div class="gt">
  <h2> Grand Total </h2>
  </div>

  <hr>

 <div className="gtflex"> 

  <div className="gttext"> 
  <h3> Subtotal: </h3> 
  </div> 

  <div className="gttext"> 
  <h3> N{{ subtotal() }}</h3> 
  </div> 

  </div> 

  <div className="gtflex"> 

  <div className="gttext"> 
  <h3> Delivery Fee: </h3> 
  </div> 

  <div className="gttext"> 
  <h3> N1,000.00</h3> 
  </div> 

  </div> 

  <div className="gtflex"> 

  <div className="gttext"> 
  <h3> Grand Total: </h3> 
  </div> 

  <div className="gttext"> 
  <h3> N{{ grandtotal() }}</h3> 
  </div> 

  </div> 

  <div className="checkout"> 
  <button> CHECKOUT </button>
  </div>
  
    <div className="mycoform">
       
       <form>

       <div className='coflex'> 

      <div>
      <label for="street address"> First name </label> <br/> <br/> 
       <input className="formname" Name="firstname" type="text"  /> 
       </div> 

       <div> 
          <label for="street address"> Last name </label> <br/> <br/> 
       <input className="formname" Name="firstname" type="text" />
       </div>

       </div> 

      <label for="street address"> Street address </label> <br/> <br/> 
       <input className="formname" id="streetaddress" Name="firstname" type="text" placeholder=" House number"/> <br/>
       <input className="formname" Name="firstname" type="text" placeholder="Street Name" /> <br/>
       <input className="formname" Name="firstname" type="text" placeholder="Nearest recognisable Bus-stop/ Area around" />  <br/>

      <label for="state"> State </label> <br/> <br/> 
       <input className="formname" id="state" Name="firstname" type="text" placeholder=" House number"/> <br/>

       <label for="phone"> Phone </label><br/> <br/> 
       <input className="formname" id="phone" Name="firstname" type="text" placeholder=" House number"/> <br/>

       <label for="email"> Email </label> <br/> <br/> 
       <input className="formname" id="email" Name="firstname" type="text" placeholder=" House number"/> <br/>

       <label for="note"> Note (Optional) </label> <br/> <br/> 
       <input className="formname" id="note" Name="firstname" type="text" placeholder=" House number"/> <br/>

       </form> 
   
    </div>

    

 </div>
   
<input type="button" v-model="moveco"  @click="checkout(mycart)">

  </div>
  

</template>

<script> 
import axios from 'axios'
export default {

     props: ['mycart', 'mylist', 'inventory', 'getprice', 'calculateprice', 'getdesc', 'subtotal', 'grandtotal', 'getimage'],
    data(){
       return{
           moveco: [],
           fee: this.subtotal(),
           gi: 1000,
           iscart: true
       }
    },
   
    methods: {
     
       checkout(){
         var cart = this.mycart
        var co = new FormData();
 
                co.append("checkout", cart);
               
               axios({
                    url: "http://localhost/Phplessons/checkout.php",
                    method: "post",
                     data: cart,
                })
        .then((response) => {
        console.log(response.data);
        }, (error) => {
        console.log(error);
});
            }
       },
}
</script> 

<style scoped src="../assets/Cart.css">
</style>
